<template>
    <v-dialog v-model="dialog" max-width="600px" persistent>
      <v-card>
        <v-card-title class="text-center">
          <span class="text-h5">Editar Cisterna</span>
        </v-card-title>
  
        <v-card-text>
          <v-form @submit.prevent="submitForm">
            <v-text-field
              v-model="form.code"
              label="Código de cisterna"
               prepend-icon="mdi mdi-shield-check"
              required
            ></v-text-field>
            <v-text-field
              v-model="form.plate"
              label="Placa"
               prepend-icon="mdi-tanker-truck"
              required
            ></v-text-field>
            <v-text-field
              v-model="form.capacity"
              label="Capacidad de Cisterna"
               prepend-icon="mdi mdi-battery-80"
              required
            ></v-text-field>
            <v-text-field
              v-model="form.documentSeries"
              label="Documento asignado"
               prepend-icon="mdi-card"
              required
            ></v-text-field>
      
  
            <v-card-actions class="justify-end">
              <v-btn text @click="closeDialog" color="#180c24">Cancelar</v-btn>
              <v-btn type="submit" color="#ff8c54">Guardar</v-btn>
            </v-card-actions>
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
  </template>
  
  <script>
  import { ref, watch } from 'vue';
  import { updateCisternsApi } from '@/api/CisternsService';
  import store from '@/store'
  import eventBus from '@/eventBus';
  export default {
    props: {
      cisterns: {
        type: Object,
        required: true,
      },
    },
    emits: ['close'],
    setup(props, { emit }) {
      const dialog = ref(true); 
  
      const form = ref({
        code: '',
        capacity: '',
        plate: '',
        //driverId: '',
        documentSeries: ''
      });
  
  
      watch(
        () => props.cisterns, 
        (newCisterns) => {
          if (newCisterns) {
            form.value = { ...newCisterns };
          }
        },
        { immediate: true }
      );
  
      const closeDialog = () => {
        emit('close');
      };
  
      // Enviar los datos para actualizar el master
      const submitForm = async () => {
        try {
          const payload = {
            code: form.value.code,
            capacity: form.value.capacity,
            plate: form.value.plate,
            documentSeries: form.value.documentSeries,
            //driverId: form.value.driverId
          };
          const token = store.state.token
          const id = form.value.cisternId; 
          await updateCisternsApi(token, payload, id); 
          eventBus.emit('success', '¡Operación completada con éxito!');
          eventBus.emit('masterCreated');
          closeDialog(); 
        } catch (error) {
            eventBus.emit('error', `Ups... hubo un error al editar el usuario:  ${ error.message}`);
          console.error('Error al actualizar el cisterns:', error.message);
        }
      };
  
      return {
        dialog,
        form,
        closeDialog,
        submitForm,
      };
    },
  };
  </script>
  
  <style scoped>
  /* Estilos específicos para el formulario de edición */
  </style>
  